*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-display: swap;
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

.container {
  width: 280px;
  margin-right: auto;
  margin-left: auto;
}

.button {
  display: inline-block;
  color: #ffffff;
  background-color: #68b738;
  border: 0;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo__name {
  margin: 0 auto;
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.page__body {
  margin: 0;
}

@media (min-width: 768px) {
  .page-header__nav {
    display: grid;
    grid-template-columns: 174px 1fr;
    column-gap: 20px;
    width: 768px;
    margin: 0 auto;
    padding: 24px 30px 0 30px;
  }
}

@media (max-width: 767px) {
  .page-header__bar {
    display: grid;
    width: 320px;
    margin: 0 auto;
    grid-template-columns: 34px 1fr 34px;
    padding: 12px 20px 16px;
    align-items: center;
  }
}

.page-header__menu {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .page-header__menu {
    margin-right: -17px;
  }
}

@media (max-width: 767px) {
  .page-header__logo {
    grid-column: 1/span 2;
  }
}
@media (min-width: 768px) {
  .page-header__logo .logo__name {
    display: none;
  }
}

.page-header__toggler {
  grid-column: -2;
  justify-self: right;
  position: relative;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggler {
    display: none;
  }
}

.page-header__toggler--close:before, .page-header__toggler--close:after {
  position: absolute;
  top: 0;
  left: 11px;
  width: 1.5px;
  height: 25px;
  content: " ";
  background-color: #000000;
}
.page-header__toggler--close:before {
  transform: rotate(45deg);
}
.page-header__toggler--close:after {
  transform: rotate(-45deg);
}

.site-menu {
  display: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  list-style-type: none;
}
@media (min-width: 768px) {
  .site-menu {
    display: flex;
    align-items: center;
    justify-self: end;
  }
}

.site-menu--open {
  display: block;
}

.site-menu__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .site-menu__item {
    border: 0;
  }
}

.site-menu__link {
  display: block;
  padding: 22px 22px 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .site-menu__link {
    padding: 17px;
    font-size: 18px;
    line-height: 24px;
  }
}

.page-main__title {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1;
}
@media (max-width: 767px) {
  .page-main__title {
    margin-bottom: 40px;
  }
}

.page-main__header {
  padding-top: 27px;
}
@media (max-width: 767px) {
  .page-main__header {
    margin-bottom: 20px;
  }
}

.page-main__subtitle {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
}

.promo {
  color: #ffffff;
  text-align: center;
  background-image: url("../../img/backtile/promo-small.jpg"), linear-gradient(#68b738, #68b738 302px, #ffffff 302px);
  background-repeat: no-repeat;
  background-position: 50% 0;
}

.promo__title {
  margin: 0;
  margin-bottom: 25px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1;
}

.promo__subtitle {
  margin: 0;
  margin-bottom: 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
}

.promo__button {
  width: 100%;
  padding: 11px 20px;
}

.catalogues {
  margin-bottom: 20px;
}

.catalogues__item {
  padding: 20px;
  background-color: #f2f2f2;
}

.catalogues__item:not(:last-of-type) {
  margin-bottom: 20px;
}

.catalogues__title {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .catalogues__title {
    margin-bottom: 29px;
  }
}

.catalogues__text {
  margin: 0;
  color: #444444;
}
@media (max-width: 767px) {
  .catalogues__text {
    margin-bottom: 23px;
  }
}

.catalogues__link {
  display: inline-block;
  width: 100%;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
}
@media (max-width: 767px) {
  .catalogues__link {
    padding-top: 15px;
    border-top: 1px solid #d9d9d9;
  }
}

.progress {
  margin-bottom: 49px;
}

.progress__list {
  margin: 0;
  padding: 0;
  color: #444444;
  list-style-type: none;
}

@media (max-width: 767px) {
  .progress__item {
    display: grid;
    grid-template-columns: 60px 1fr;
    column-gap: 20px;
  }
  .progress__item + .progress__item {
    margin-top: 20px;
  }
}

.progress__item::before {
  content: " ";
  width: 60px;
  height: 60px;
  margin-top: -2px;
  background-color: #68b738;
}

.example {
  padding: 25px 0 40px 0;
  background-color: #ebebeb;
}

.example__text {
  margin: 0;
  margin-bottom: 20px;
  color: #444444;
}

.example__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin: 0;
  margin-bottom: 20px;
}

.example__item {
  display: flex;
  align-items: center;
  flex-direction: column-reverse;
  row-gap: 8px;
  padding-top: 14px;
  border-top: 1px solid #cdcdcd;
}

.example__term {
  width: 77px;
  font-size: 12px;
  line-height: 1;
  color: #444444;
  text-transform: lowercase;
  text-align: center;
}

.example__desc {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1;
  text-align: center;
}

.example__costs {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}

.example__images {
  margin-bottom: 15px;
}

.example__image {
  display: block;
  margin: 0 auto;
}

.example__switcher {
  margin: 0 auto;
}

.switcher {
  display: grid;
  grid-template-columns: 49px 1fr 49px;
  width: 210px;
}

.switcher__button {
  border: none;
  background-color: transparent;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.switcher__button--before {
  grid-column: 1/2;
}

.switcher__button--after {
  grid-column: 3/4;
}

.page-footer {
  background-color: #f2f2f2;
}

.page-footer__logo {
  padding: 39px 0 32px 0;
}

.page-footer__social {
  margin: 0;
  padding: 20px 0;
}

.page-footer__academy {
  padding: 22px 0;
}

@media (max-width: 767px) {
  .contacts {
    background-color: #ffffff;
  }
}

.contacts__address {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 280px;
  margin: 0 auto;
  padding: 26px 0;
  font-style: normal;
}

.contacts__title {
  margin: 0;
  padding-right: 12px;
  color: #111111;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.contacts__text {
  margin: 0;
  line-height: 20px;
  color: #444444;
}

.contacts__map {
  display: block;
  width: 100%;
}

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 33px;
  list-style: none;
  border-top: 1px solid #cdcdcd;
  border-bottom: 1px solid #cdcdcd;
}

.social__link {
  display: inline-block;
  width: 22px;
  height: 22px;
  background-color: #cdcdcd;
}

.academy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  color: #444444;
  text-decoration: none;
}

.products {
  padding: 0 20px;
}

.products__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #222222;
}

.card__link {
  text-decoration: none;
}

.card__list {
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.card__desc {
  margin: 0;
}

.card__button {
  font-size: 16px;
  line-height: 20px;
}

.more-products__title {
  font-size: 24px;
}

.more-products__list {
  padding: 0;
  list-style-type: none;
}

.card-row__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
}

.card-row__list {
  line-height: 1;
  color: #444444;
}

.card-row__desc {
  margin: 0;
}

.card-row__button {
  font-size: 16px;
  line-height: 20px;
}

.bonus {
  padding: 60px 60px 40px 60px;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  background-image: url("../../img/backtile/bonus.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #68b738;
}

.form {
  padding: 0 20px;
}

.form__notice {
  line-height: 16px;
}

.fieldset {
  padding: 0;
  border: 0;
}

.fieldset__list {
  padding: 0;
  list-style-type: none;
}

.fieldset__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
}

.field {
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.field__input {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  text-transform: inherit;
}

.select {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
.select--radio {
  color: #222222;
}